import { Container, ValueCard, ValuesGrid, ValuesLead, ValuesSection, ValuesTitle, ValueText, ValueTitle } from "./Values.styled";

export const Values = () => {
  return (
    <ValuesSection>
      <Container>
        <ValuesTitle  initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, ease: "easeOut" }}
          viewport={{ once: true, amount: 0.2 }}> </ValuesTitle>
        <ValuesLead  initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true, amount: 0.2 }}>
          ,      ,     
          .
        </ValuesLead>
        <ValuesGrid  initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, ease: "easeOut" }}
          viewport={{ once: true, amount: 0.2 }}>
          <ValueCard>
            <ValueTitle>  </ValueTitle>
            <ValueText>
               ,  ,     
              .
            </ValueText>
          </ValueCard>
          <ValueCard>
            <ValueTitle>  </ValueTitle>
            <ValueText>
               , ,        .
            </ValueText>
          </ValueCard>
          <ValueCard>
            <ValueTitle>   </ValueTitle>
            <ValueText>
                ,      
              .
            </ValueText>
          </ValueCard>
        </ValuesGrid>
      </Container>
    </ValuesSection>
  );
};
