import {
  Header,
  Container,
  NavList,
  Logo,
  NavLink,
  BtnHeader,
  BtnBasket,
  BtnMobMenu,
} from "./header.styled";
import { ShopBascetIcon } from "../../icons/shopBascetIcon";
import { MobMenuIcon } from "../../icons/mobMenuIcon";
import { Outlet } from "react-router-dom";
import { useState } from "react";
import { MobMenu } from "../MobMenu/MobMenu";
export const SideBar = () => {
 const [isOpen, setIsOpen] = useState(false);
  const handleMobMenuClick = () => {
    setIsOpen(!isOpen);
  }

  return (
    <>
    <Header>
      <Container>
        <BtnMobMenu onClick={handleMobMenuClick} type="button">
          <MobMenuIcon/>
        </BtnMobMenu>
        <Logo href="/"></Logo>
        <nav>
          <NavList>
            <li>
              <NavLink to="/"></NavLink>
            </li>
            <li>
              <NavLink to="/menu"></NavLink>
            </li>
            <li>
              <NavLink to="/about"> </NavLink>
            </li>
            <li>
              <NavLink to="/"></NavLink>
            </li>
          </NavList>
        </nav>
        <BtnHeader type="button"> </BtnHeader>
        <BtnBasket href="0">
          <ShopBascetIcon />
        </BtnBasket>
      </Container>
      <MobMenu open={isOpen} clouseMobMenu={handleMobMenuClick}/>
    </Header>
    <Outlet />
</>
  );
};
